@import 'normalize.css';

//font embeded
@font-face {
    font-family: ProximaNova;
    src: url('/assets/fonts/Proxima-Nova-Bold.woff');
    font-weight: 700;
}
@font-face {
    font-family: ProximaNova;
    font-weight: 400;
    src: url('/assets/fonts/Proxima-Nova-Regular.woff2');
}
@font-face {
    font-family: ProximaNova;
    font-weight: 600;
    src: url('/assets/fonts/ProximaNova-Semibold.woff2');
}
@font-face {
    font-family: SofiaPro;
    font-weight: 700;
    src: url('/assets/fonts/sofiapro-bold.woff');
}
:root {
    --primary: #fe2c55;

    //default layout
    --default-layout-header-hight: 60px;
    --default-layout-width: 1150px;
    --default-layout-horizontal-spacer: 24px;
    --black: #000;
    --white: #fff;
    --text-color: #333;
    --font-family: 'ProximaNova', sans-serif;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
}

body {
    font-family: var(--font-family);
    font-size: 1.6rem;
    line-height: 1.5;
    color: var(--text-color);
    overflow: overlay;
    &.hidden {
        overflow: hidden;
    }
    &.video-modal {
        overflow: hidden;
    }
}

// scroll bar

html *::-webkit-scrollbar {
    border-radius: 0;
    width: 8px;
}

html *::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: rgba(22, 24, 35, 0.06);
}

html *::-webkit-scrollbar-track {
    border-radius: 0;
    background-color: rgba(0, 0, 0, 0);
}

input,
button,
[tabindex] {
    outline: none;
    border: none;
}

a[href] {
    text-decoration: none;
}

//custom tippy tooltip

body {
    .tippy-box {
        background-color: rgba(84, 84, 84, 0.92);
        font-size: 1.6rem;
        font-weight: 600;
        border-radius: 8px;
        line-height: 2.2rem;
    }
    .tippy-arrow {
        color: rgba(84, 84, 84, 0.92);
        top: -8px;
    }
    .tippy-content {
        padding: 8px 9px;
    }
}
